import 'uni-pages';import '@dcloudio/uni-stat';
import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false

// 服务器地址挂载到vue原型上(方式二)
Vue.prototype.serverUrl="https://www.imovietrailer.com/superhero"          //生产环境
// Vue.prototype.serverUrl="https://www.imovietrailer-dev.com/superhero"   //开发环境

//挂载注册/登录判断
Vue.prototype.getGlobalUser = function (key) {
	let userInfo = uni.getStorageSync("globalUser");
	if (userInfo != null && userInfo != "" && userInfo != undefined) {
		return userInfo;
	} else {
		return null;
	}
}

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()